import uniRate from "@/components/uni-rate/uni-rate.vue"
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniCollapse from "@/components/uni-collapse/uni-collapse.vue"
import uniCollapseItem from "@/components/uni-collapse-item/uni-collapse-item.vue"

export default {
	data() {
		return {
			userLogined:false,
			userInfo:{},
			noticed:true,
			Active:false
		}
	},
	onShow() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		if (userinfo != null) {
			me.userLogined = true;
			me.userInfo = userinfo;
			// console.log(me.userInfo);
		}else{
			me.userLogined = false;
			me.userInfo = {};
		};
		if(this.notice_num == 0){
			this.noticed = false;
		}
	},
	methods: {
		login(){
			uni.navigateTo({
				url:"../me_login/me_login"
			})
		}, 
		previewImg(){
			var me = this;
			var globalUser = me.getGlobalUser("userInfos");
			// console.log(globalUser);
			var faceArr = [];
			faceArr.push(globalUser.avatarUrl);
			uni.previewImage({
				urls:faceArr,
				current: faceArr[0]
			})
		},
		edit_me(){
			uni.navigateTo({
				url:"../me_notice/me_notice"
			})
		},
		logout(){
			var globalUser = this.getGlobalUser("userInfos");
			uni.removeStorageSync("userInfos");
			uni.switchTab({
				url:"../main/main"
			});
		},
	},
	components: {
		uniRate,
		uniList,
		uniListItem,
		uniCollapse,
		uniCollapseItem
	}
}
